import React, { useState, useEffect } from 'react'
import { useHistory } from 'react-router-dom'
import { Input, Form, Button, Row, Col, message, Divider, Checkbox, Select } from 'antd'
import PasswordChecklist from 'react-password-checklist'
import AlertWait from './AlertWait'
import Logo from '../components/Logo'
import md5 from 'md5'
import CuentaService from './../service/CuentaService'
import CatalogoService from './../service/CatalogoService'

const cuentaService = new CuentaService()
const catalogoService = new CatalogoService()
const formItemLayout = { xs: 24, sm: 12, md: 12, lg: 12, xl: 12 }
const { Option } = Select

const RegisterPYME = () => {

    const navigate = useHistory()
    const [isLoading, setIsLoading] = useState(true)
    const [error, setError] = useState('')
    const [form] = Form.useForm()

    const [password, setPassword] = useState("")
    const [passwordConfirm, setPasswordConfirm] = useState("")

    const [tiposPersona, setTiposPersona] = useState([])

    const [tiposIdentificacion, setTiposIdentificacion] = useState([])

    const [actividades, setActividades] = useState([])

    const renderError = (e) => <div key={e.messageCode}>{e.messageText}</div>
    const renderCatalogo = e => <Option key={e.catId} value={e.catId}>{e.catNombre}</Option>
    const renderCatalogoValor = e => <Option key={e.catValor} value={e.catValor}>{e.catNombre}</Option>

    const login = () => {
        navigate.push('/login')
    }
    const registrarInversionista = () => {
        navigate.push('/register')
    }

    const registrar = (values) => {
        setError('')
        const nuevaPersona = { ...values, perPassword: md5(values.perPassword) }
        setIsLoading(true)
        cuentaService
            .registerPersona({ datos: { ...nuevaPersona } })
            .then(response => {
                const { data } = response
                if (data && data.listObject.length > 0 && data.listObject[0].perId) {
                    message.success('Informacin registrada correctamente')
                }
                setIsLoading(false)
                navigate.push('/login')
            })
            .catch(error => {
                setIsLoading(false)
                const { status, data } = error.response
                switch (status) {
                    case 409:
                        setError(data.map(e => renderError(e)))
                        break
                    case 500:
                        setError(data)
                        break
                    default:
                        break
                }
            })
    }

    useEffect(() => {
        if (isLoading) {
            catalogoService
                .getTiposIdentificacion()
                .then(response => {
                    const { data } = response
                    setTiposIdentificacion(data.listObject[0])
                    catalogoService
                        .getTiposPersona()
                        .then(response => {
                            const { data } = response
                            setTiposPersona(data.listObject[0])
                            catalogoService
                                .getActividadesEconomicas()
                                .then(response => {
                                    setIsLoading(false)
                                    const { data } = response
                                    setActividades(data.listObject[0])
                                })
                                .catch(error => {
                                    setIsLoading(false)
                                    const { response } = error
                                    setError(response.status === 409 ? response.data[0].messageText : "No pudimos hacer la solicitud para obtener el catlogo")
                                })
                        })
                        .catch(error => {
                            setIsLoading(false)
                            const { response } = error
                            setError(response.status === 409 ? response.data[0].messageText : "No pudimos hacer la solicitud para obtener el catlogo")
                        })
                })
                .catch(error => {
                    setIsLoading(false)
                    const { response } = error
                    setError(response.status === 409 ? response.data[0].messageText : "No pudimos hacer la solicitud para obtener el catlogo")
                })
        }
    }, [isLoading])

    return (
        <Row justify='center' style={{ backgroundColor: '#F0F0F0' }}>
            <Col xs={22} sm={22} md={18} lg={12} xl={12}>
                <Logo />
                <Divider><span style={{ textTransform: 'uppercase', letterSpacing: '1px' }}>Registrarme como</span></Divider>
                <Row gutter={[10, 10]} style={{ paddingBottom: '20px' }}>
                    <Col span={12}>
                        <Button type='default' onClick={(e) => registrarInversionista()} block>INVERSIONISTA</Button>
                    </Col>
                    <Col span={12}>
                        <Button type='primary' block>PERSONA</Button>
                    </Col>
                </Row>
                <Form
                    layout={'vertical'}
                    form={form}
                    initialValues={
                        {
                            perIdTipPer: "",
                            perNombres: "",
                            perApellidos: "",
                            perIdActividad: "",
                            perClientes: "",
                            perDescripcion: "",
                            perEmail: "",
                            perPassword: "",
                            perRazonSocial: "",
                            perSitioWeb: "",
                            perDocumento: "",
                            perTipoDoc: "",
                            perDireccion: "",
                            perTelefono: "",
                            perPasswordConfirm: "",
                            perTerCon: false
                        }
                    }
                    onFinish={registrar}
                >
                    <Row gutter={[10, 10]}>
                        <Col span={24}>
                            <Form.Item label="Tipo de Persona"
                                name='perIdTipPer'
                                rules={[
                                    {
                                        required: true,
                                        message: 'Seleccione el tipo de persona',
                                    },
                                ]}
                            >
                                <Select>
                                    {
                                        tiposPersona && tiposPersona.map(p => renderCatalogo(p))
                                    }
                                </Select>
                            </Form.Item>
                        </Col>
                    </Row>
                    <Row gutter={[10, 10]}>
                        <Col {...formItemLayout}>
                            <Form.Item label="Tipo de Identificacin"
                                name='perTipoDoc'
                                rules={[
                                    {
                                        required: true,
                                        message: 'Selecccione el tipo de identificacin',
                                    },
                                ]}
                            >
                                <Select>
                                    {
                                        tiposIdentificacion && tiposIdentificacion.map(p => renderCatalogoValor(p))
                                    }
                                </Select>
                            </Form.Item>
                        </Col>
                        <Col {...formItemLayout}>
                            <Form.Item label="Identificacin"
                                name='perDocumento'
                                rules={[
                                    {
                                        required: true,
                                        message: 'Ingrese su identificacin',
                                    },
                                ]}
                            >
                                <Input id="perDocumento" name="perDocumento" label='Identificacin' type='text' />
                            </Form.Item>
                        </Col>
                    </Row>
                    <Row gutter={[10, 10]}>
                        <Col span={24}>
                            <Form.Item label="Razn Social"
                                name='perRazonSocial'
                                rules={[
                                    {
                                        required: true,
                                        message: 'Ingrese la razn social',
                                    },
                                ]}
                            >
                                <Input label='Razn Social' type='text' />
                            </Form.Item>
                        </Col>
                    </Row>
                    <Row gutter={[10, 10]}>
                        <Col {...formItemLayout}>
                            <Form.Item label="Tipo de Actividad"
                                name='perIdActividad'
                                rules={[
                                    {
                                        required: true,
                                        message: 'Seleccione el tipo de actividad',
                                    },
                                ]}
                            >
                                <Select>
                                    {
                                        actividades && actividades.map(p => renderCatalogo(p))
                                    }
                                </Select>
                            </Form.Item>
                        </Col>
                        <Col {...formItemLayout}>
                            <Form.Item label="N Clientes"
                                name='perClientes'
                                rules={[
                                    {
                                        required: true,
                                        message: 'Ingrese el nmero de clientes',
                                    },
                                ]}
                            >
                                <Input label='N Clientes' type='number' />
                            </Form.Item>
                        </Col>
                    </Row>
                    <Row gutter={[10, 10]}>
                        <Col span={24}>
                            <Form.Item label="Direccin"
                                name='perDireccion'
                                rules={[
                                    {
                                        required: true,
                                        message: 'Ingrese la direccin',
                                    },
                                ]}
                            >
                                <Input label='Direccin' type='text' />
                            </Form.Item>
                        </Col>
                    </Row>
                    <Row gutter={[10, 10]}>
                        <Col {...formItemLayout}>
                            <Form.Item label="Telfono"
                                name='perTelefono'
                                rules={[
                                    {
                                        required: true,
                                        message: 'Ingrese el telfono',
                                    },
                                ]}
                            >
                                <Input label='Telfono' type='text' />
                            </Form.Item>
                        </Col>
                        <Col {...formItemLayout}>
                            <Form.Item label="Sitio WEB"
                                name='perSitioWeb'
                                rules={[
                                    {
                                        required: true,
                                        message: 'Ingrese el sitio web',
                                    },
                                ]}
                            >
                                <Input label='Sitio WEB' type='text' />
                            </Form.Item>
                        </Col>
                    </Row>
                    <Row gutter={[10, 10]}>
                        <Col span={24}>
                            <Form.Item label="Descripcin"
                                name='perDescripcion'
                                rules={[
                                    {
                                        required: true,
                                        message: 'Ingrese la descripcin',
                                    },
                                ]}
                            >
                                <Input label='Descripcin' type='text' />
                            </Form.Item>
                        </Col>
                    </Row>
                    <Row gutter={[10, 10]}>
                        <Col span={24}>
                            <Form.Item label="Email"
                                name='perEmail'
                                rules={[
                                    {
                                        required: true,
                                        message: 'Ingrese el email',
                                    },
                                ]}
                            >
                                <Input id="perEmail" name="perEmail" label='Email' type='text' />
                            </Form.Item>
                        </Col>
                    </Row>
                    <Row gutter={[10, 10]}>
                        <Col {...formItemLayout}>
                            <Form.Item label="Nombres"
                                name='perNombres'
                                rules={[
                                    {
                                        required: true,
                                        message: 'Ingrese sus nombres',
                                    },
                                ]}
                            >
                                <Input id="perNombres" name="perNombres" label='Nombres' type='text' />
                            </Form.Item>
                        </Col>
                        <Col {...formItemLayout}>
                            <Form.Item label="Apellidos"
                                name='perApellidos'
                                rules={[
                                    {
                                        required: true,
                                        message: 'Ingrese sus apellidos',
                                    },
                                ]}
                            >
                                <Input id="perApellidos" name="perApellidos" label='Apellidos' type='text' />
                            </Form.Item>
                        </Col>
                    </Row>

                    <Row gutter={[10, 10]}>
                        <Col {...formItemLayout}>
                            <Form.Item label="Contrasea"
                                name='perPassword'
                                rules={[
                                    {
                                        required: true,
                                        message: 'Ingrese la contrasea',
                                    },
                                ]}
                            >
                                <Input.Password id="perPassword" name="perPassword" label='Contrasea' onChange={e => setPassword(e.target.value)} />
                            </Form.Item>
                        </Col>
                        <Col {...formItemLayout}>
                            <Form.Item label="Confirme la contrasea"
                                name='perPasswordConfirm'
                                rules={[
                                    {
                                        required: true,
                                        message: 'Confirme la contrasea',
                                    },
                                ]}
                            >
                                <Input.Password id="perPasswordConfirm" name="perPasswordConfirm" label='Confirme la contrasea' onChange={e => setPasswordConfirm(e.target.value)} />
                            </Form.Item>
                        </Col>
                        <Col span={24}>
                            <PasswordChecklist
                                rules={["minLength", "specialChar", "number", "capital", "match"]}
                                minLength={8}
                                value={password}
                                valueAgain={passwordConfirm}
                                messages={{
                                    minLength: "La contrasea tiene ms de 8 caracteres.",
                                    specialChar: "La contrasea tiene caracteres especiales.",
                                    number: "La contrasea tiene un nmero.",
                                    capital: "La contrasea tiene una letra mayscula.",
                                    match: "Las contraseas coinciden.",
                                }}
                                onChange={(isValid) => { }}
                            />
                        </Col>
                        <Col span={24}>
                            <Form.Item
                                name='perTerCon'
                                valuePropName="checked"
                                rules={[
                                    {
                                        validator: (_, value) =>
                                            value ? Promise.resolve() : Promise.reject(new Error('Acepte los trminos y condiciones y el acuerdo de uso de la plataforma')),
                                    },
                                ]}
                            >
                                <Checkbox id='perTerCon' name='perTerCon'>
                                    Estoy de acuerdo y acepto los <a href='/files/terminoscondiciones.pdf' target={'_blank'}>Trminos y Condiciones</a> y el <a href='/files/politicasuso.pdf' target={'_blank'}>Acuerdo de Uso</a> de la Plataforma CredKlub que incluye la Plitica de Privacidad aplicable al tratamiento de la informacin de datos personales.
                                </Checkbox>
                            </Form.Item>
                        </Col>
                    </Row>
                    <Form.Item>
                        <Button type='primary' htmlType='submit' loading={isLoading} disabled={isLoading} block>Registrar</Button>
                    </Form.Item>
                </Form>
                <AlertWait error={error} isLoading={isLoading} />
                <Row justify='center' style={{ paddingTop: '15px', marginBottom: '25px' }}>
                    <Col>
                        Ya ests registrado? <Button type='link' onClick={login}>Ingresa aqu</Button>
                    </Col>
                </Row>
            </Col>
        </Row>
    )
}

export default RegisterPYME