import { format } from 'date-fns'
import { es } from 'date-fns/esm/locale'
import locale from "antd/es/date-picker/locale/es_ES";
export const formatoMoneda = (v) => {
    const formater = new Intl.NumberFormat('es-EC', {
        style: 'currency',
        currency: 'USD'
    })
    return formater.format(v)
}
export const formatoFechas = f => {
    const formater = new Intl.DateTimeFormat('es-EC', {
        year: 'numeric',
        month: 'long',
        day: '2-digit'
    })
    return formater.format(new Date(f.toString().substring(0, 10).split('-')[0], f.toString().substring(0, 10).split('-')[1] - 1, f.toString().substring(0, 10).split('-')[2]))
}
const toCapitalize = (o) => o.toLowerCase().split(' ').map(r => `${r.substring(0, 1).toUpperCase()}${r.substring(1, r.length)} `)
export const formatoFecha = (f) => format(new Date(f.toString().substring(0, 10).split('-')[0], f.toString().substring(0, 10).split('-')[1] - 1, f.toString().substring(0, 10).split('-')[2]), "dd MMMM yyyy", { locale: es })
export const formatoFechaDate = (f) => format(f, "dd/MM/yyyy", { locale: es })
export const getToken = () => localStorage.getItem('ckToken') || ''
export const getDatosUsuario = () => localStorage.getItem('ckDatosUsuario') ? JSON.parse(localStorage.getItem('ckDatosUsuario')) : { opciones: [{ actionOpcion: "/", blnEscritura: false, blnLectura: false, etiquetaOpcion: "MGE", idOpcion: 1, nivel: 1, orden: 1 }] }
export const getResponsable = () => getDatosUsuario().idPymeInv
const getSolicitudes = () => localStorage.getItem('ckSolicitudes') ? JSON.parse(localStorage.getItem('ckSolicitudes')) : []
export const getSolicitudUsuarioOferta = (idOfertaInv) => {
    const ckSolicitudes = getSolicitudes()
    const idUsuario = parseInt(getDatosUsuario().idUsuario)
    const usuario = ckSolicitudes.filter(u => u.idUsuario === idUsuario)
    const solicitudUsuarioOferta = usuario.length === 1 ? usuario[0].solicitudes.filter(s => s.idOfertaInv === parseInt(idOfertaInv)) : []
    return solicitudUsuarioOferta
}
