import axios from 'axios'
import AxiosInstance from "./../utils/AxiosInstance"
import { getOfertasInversionUrl, getOfertaInversionByIdUrl } from "../utils/urls"
export default class OfertaInversionService {
    gets = () => {
        return axios.get(getOfertasInversionUrl())
    }
    get = (props) => {
        const { navigate, ofertaInversion } = props
        return AxiosInstance(navigate).post(getOfertaInversionByIdUrl(), ofertaInversion)
    }
}