import React from 'react'
import { useHistory } from 'react-router-dom'
import { Row, Col } from 'antd'

const logo = {
    fontSize: "2.5em",
    padding: 0,
    margin: '0 0 20px 0',
    lineHeight: "1em"
}

const Logo = (props) => {
    const history = useHistory()
    const go = () => history.push("/")
    return (
        <Row justify='center' {...props}>
            <Col>
                <div className="mge mge-bold" style={logo} onClick={() => go}>
                    <span className="mge-lightGreen">
                        My<br />Green
                    </span><br/>
                    <span className="mge-green">Enterprise</span>
                </div>
            </Col>
        </Row>
    )
}

export default Logo